FCBH.docReady(() => {
  const checkoutForm = FCBH.getFromStorage(FCBH.CHECKOUT_FORM);
  if (!checkoutForm) {
    window.location = "/store";
    return;
  }
  const order = new OrderConfirmation(checkoutForm);
  order.render();
});

class OrderConfirmation {
  subtotal = document.querySelector("#subtotal");
  total = document.querySelector("#total");
  email = document.querySelector("#email");
  creditCard = document.querySelector("#credit-card");
  itemList = document.querySelector("#order-items");

  //Shipping
  shippingName = document.querySelector("#shipping-name");
  shippingAddress1 = document.querySelector("#shipping-address-1");
  shippingAddress2 = document.querySelector("#shipping-address-2");
  shippingCity = document.querySelector("#shipping-city");

  //Billing
  billingName = document.querySelector("#billing-name");
  billingAddress1 = document.querySelector("#billing-address-1");
  billingAddress2 = document.querySelector("#billing-address-2");
  billingCity = document.querySelector("#billing-city");

  constructor(checkoutForm) {
    this.checkoutForm = checkoutForm;
  }

  render() {
    this.renderTotalSection();
    this.renderCustomerInfoSection();
    this.renderBillingInfoSection();
    this.renderItems();
  }

  renderTotalSection() {
    this.subtotal.textContent = `$${this.checkoutForm.total_cost} USD`;
    this.total.textContent = `$${this.checkoutForm.total_cost} USD`;
  }

  renderCustomerInfoSection() {
    this.email.textContent = this.checkoutForm.email;
    this.shippingName.textContent = this.checkoutForm.shipping_address.full_name;
    this.shippingAddress1.textContent = this.checkoutForm.shipping_address.address;
    this.shippingAddress2.textContent = this.checkoutForm.shipping_address.address_two;
    this.shippingCity.textContent = this.checkoutForm.shipping_address.city;
  }

  renderBillingInfoSection() {
    this.creditCard.textContent = `Credit Card **** ${this.checkoutForm.card_last_digits}`;
    this.billingName.textContent = this.checkoutForm.billing_address.full_name;
    this.billingAddress1.textContent = this.checkoutForm.billing_address.address;
    this.billingAddress2.textContent = this.checkoutForm.billing_address.address_two;
    this.billingCity.textContent = this.checkoutForm.billing_address.city;
  }

  renderItems() {
    while (this.itemList.lastChild) {
      this.itemList.removeChild(this.itemList.lastChild);
    }
    const nodes = this.checkoutForm.items.map((item, i) => {
      return `
      <div class="order-item" data-id="${i}" data-qty="${item.quantity}">
        <img src="https://cdn.faithcomesbyhearing.com/resources/fcbh/img/store/products/${
          item.thumbnail
        }" alt="" class="order-item-image loaded" loading="lazy" data-ll-status="loaded">
        <div class="order-item-info">
          <div class="order-item-title">${item.title}</div>
          <div class="txt-sm txt-qty">
          ${item.version}
          </div>
          <div class="txt-sm txt-qty">
          ${item.language}
          </div>
          <div class="order-item-qty">
            <div class="txt-sm txt-qty">Quantity: </div>
            <div class="txt-sm txt-qty">${item.quantity}</div>
          </div>
        </div>
        <div class="order-item-price txt-md">$${item.subtotal.toFixed(2)}USD</div>
      </div>
      `;
    });
    this.itemList.innerHTML = nodes.join("");
  }
}
